export default {
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			flist: [],
			slist: [],
			tlist: [],
		}
	},
	onLoad(){
		this.loadData();
	},
	methods: {
		async loadData(){
			console.log(123);
			let list = await this.Api.getItemType();
			list.data.forEach(item=>{
				if(item.series==1){
					this.flist.push(item);
				} else if (item.series==2) {
					this.slist.push(item);
				} else if (item.series==3) {
					this.tlist.push(item);
				}
			})
			this.currentId = this.flist[0]._id;
		},
		//一级分类点击
		tabtap(item){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			this.currentId = item._id;
			let index = this.slist.findIndex(sitem=>sitem.pid === item._id);
			this.tabScrollTop = this.slist[index].top;
		},
		//右侧栏滚动
		asideScroll(e){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			if(tabs.length > 0){
				this.currentId = tabs[0].pid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			let h = 0;
			this.slist.forEach(item=>{
				let view = uni.createSelectorQuery().select("#main-" + item._id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(sid, tid){
			uni.navigateTo({
				url: `/pages/product/list?fid=${this.currentId}&sid=${sid}&tid=${tid}`
			})
		}
	}
}
